import { DurableObject } from "cloudflare:workers";

interface Env {
  PROCESS_MEETING_WORKFLOW: Workflow;
  MEETING_ROOM: DurableObjectNamespace;
}

interface SessionData {
  name: string;
  role: string;
}

export class MeetingRoom extends DurableObject {
  state: DurableObjectState;
  env: Env;
  sessions: Map<WebSocket, SessionData>;
  transcript: { sender: string; text: string; timestamp: number }[];
  password: string;
  hostName: string;

  constructor(state: DurableObjectState, env: Env) {
    super(state, env);
    this.state = state;
    this.env = env;
    this.sessions = new Map();
    this.transcript = [];
    this.password = "";
    this.hostName = "";

    this.state.blockConcurrencyWhile(async () => {
      const storedMeta = await this.state.storage.get<{ password: string; hostName: string }>("meta");
      if (storedMeta) {
        this.password = storedMeta.password;
        this.hostName = storedMeta.hostName;
      }
    });
  }

  async fetch(request: Request): Promise<Response> {
    const url = new URL(request.url);

    if (url.pathname === "/init" && request.method === "POST") {
      const body = await request.json() as { password: string; hostName: string };
      this.password = body.password;
      this.hostName = body.hostName;
      await this.state.storage.put("meta", body);
      return new Response("Initialized");
    }

    if (url.pathname === "/check-auth" && request.method === "POST") {
      const body = await request.json() as { password?: string };
      // If no password is set on the room, treat as OK.
      if (!this.password || body.password === this.password) {
        return new Response("OK", { status: 200 });
      }
      return new Response("Unauthorized", { status: 401 });
    }

    // Return metadata about the room (e.g., whether it requires a password)
    if (url.pathname === "/meta" && request.method === "GET") {
      const meta = { requiresPassword: !!this.password, hostName: this.hostName };
      return new Response(JSON.stringify(meta), { status: 200, headers: { 'Content-Type': 'application/json' } });
    }

    // Metadata endpoint for external callers to determine if a password is required
    if (url.pathname === "/meta" && request.method === "GET") {
      return new Response(JSON.stringify({ requiresPassword: !!this.password, hostName: this.hostName }), { headers: { "Content-Type": "application/json" } });
    }

    if (request.headers.get("Upgrade") === "websocket") {
      const tokenStr = url.searchParams.get("token");
      if (!tokenStr) return new Response("Missing token", { status: 400 });

      const user = JSON.parse(tokenStr);
      const [client, server] = Object.values(new WebSocketPair());

      this.ctx.acceptWebSocket(server);
      this.sessions.set(server, { name: user.name, role: user.role });

      this.broadcast({
        type: "USER_JOINED",
        data: { name: user.name, totalUsers: this.sessions.size }
      });

      return new Response(null, { status: 101, webSocket: client });
    }

    return new Response("Not Found", { status: 404 });
  }

  async webSocketMessage(ws: WebSocket, message: string) {
    const data = JSON.parse(message);
    const user = this.sessions.get(ws);

    if (!user) return;

    if (data.type === "SEND_TRANSCRIPT") {
      const entry = {
        sender: user.name,
        text: data.payload.text,
        timestamp: Date.now()
      };
      this.transcript.push(entry);
      
      this.broadcast({
        type: "NEW_MESSAGE",
        data: entry
      });
    }

    if (data.type === "END_SESSION" && user.role === "HOST") {
      this.broadcast({ type: "MEETING_ENDED" });
      
      await this.env.PROCESS_MEETING_WORKFLOW.create({
        params: {
          transcript: this.transcript,
          roomId: this.state.id.toString(),
          hostName: this.hostName
        }
      });
    }
  }

  async webSocketClose(ws: WebSocket, code: number, reason: string, wasClean: boolean) {
    const user = this.sessions.get(ws);
    this.sessions.delete(ws);
    if (user) {
      this.broadcast({
        type: "USER_LEFT",
        data: { name: user.name, totalUsers: this.sessions.size }
      });
    }
  }

  broadcast(message: any) {
    const msg = JSON.stringify(message);
    for (const session of this.sessions.keys()) {
      try {
        session.send(msg);
      } catch (err) {
        this.sessions.delete(session);
      }
    }
  }
}

// Default module worker export: route requests to the MeetingRoom Durable Object.
export default {
  async fetch(request: Request, env: Env, ctx: ExecutionContext) {
    const url = new URL(request.url);
    const parts = url.pathname.split("/").filter(Boolean);

    // API: create a new room -> POST /api/create-room  { password?, hostName? }
    if (url.pathname === "/api/create-room" && request.method === "POST") {
      const body = await request.json().catch(() => ({})) as { password?: string; hostName?: string };
      const password = body.password || "";
      const hostName = body.hostName || "";

      // generate a short room id
      const roomId = (globalThis.crypto && typeof globalThis.crypto.randomUUID === "function")
        ? globalThis.crypto.randomUUID().split("-")[0]
        : Math.random().toString(36).slice(2, 9);

      const id = env.MEETING_ROOM.idFromName(roomId);
      const obj = env.MEETING_ROOM.get(id);

      // initialize the Durable Object with the room metadata
      const initReq = new Request(`https://init.local/room/${roomId}/init`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ password, hostName })
      });

      await obj.fetch(initReq);

      return new Response(JSON.stringify({ roomId }), {
        status: 201,
        headers: { "Content-Type": "application/json" }
      });
    }

    // API: join a room -> POST /api/join-room { roomId, userName|name, role?, password? }
    if (url.pathname === "/api/join-room" && request.method === "POST") {
      const body = await request.json().catch(() => ({})) as { roomId?: string; name?: string; userName?: string; role?: string; password?: string };
      const { roomId, role: providedRole, password } = body;
      const name = (body.name || body.userName || "").trim();
      const role = (providedRole && providedRole.trim()) ? providedRole.trim() : "ATTENDEE";

      if (!roomId || !name) {
        return new Response(JSON.stringify({ error: 'missing parameters', missing: { roomId: !roomId, name: !name } }), { status: 400, headers: { 'Content-Type': 'application/json' } });
      }

      const id = env.MEETING_ROOM.idFromName(roomId);
      const obj = env.MEETING_ROOM.get(id);

      // Determine whether the room requires a password
      const metaReq = new Request(`https://meta.local/room/${roomId}/meta`, { method: 'GET' });
      let requiresPassword = false;
      try {
        const metaRes = await obj.fetch(metaReq);
        if (metaRes.ok) {
          const metaJson = await metaRes.json().catch(() => ({})) as { requiresPassword?: boolean; hostName?: string };
          console.log('join-room meta:', { status: metaRes.status, ok: metaRes.ok, metaJson });
          requiresPassword = !!metaJson.requiresPassword;
        } else {
          console.log('join-room meta fetch not ok', { status: metaRes.status });
        }
      } catch (err) {
        console.log('join-room meta fetch error', err);
        // If meta lookup fails, default to not requiring a password (fail-open for usability in local dev)
        requiresPassword = false;
      }

      if (requiresPassword && password === undefined) {
        return new Response(JSON.stringify({ error: 'password required' }), { status: 401, headers: { 'Content-Type': 'application/json' } });
      }

      // If a password was provided, validate it with the DO
      if (password !== undefined) {
        const authReq = new Request(`https://check.local/room/${roomId}/check-auth`, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ password })
        });
        const authRes = await obj.fetch(authReq);
        if (authRes.status !== 200) {
          return new Response(JSON.stringify({ error: 'unauthorized' }), { status: 401, headers: { 'Content-Type': 'application/json' } });
        }
      }

      const token = encodeURIComponent(JSON.stringify({ name, role }));
      const wsUrl = `ws://127.0.0.1:8787/room/${roomId}?token=${token}`;
      return new Response(JSON.stringify({ token, wsUrl }), { status: 200, headers: { 'Content-Type': 'application/json' } });
    }

    // Expect URLs like /room/<roomId>/init or /room/<roomId>/check-auth or /room/<roomId>
    if (parts[0] === "room" && parts[1]) {
      const roomId = parts[1];
      const remaining = parts.slice(2);
      url.pathname = remaining.length ? `/${remaining.join("/")}` : "/";

      const forward = new Request(url.toString(), request);
      const id = env.MEETING_ROOM.idFromName(roomId);
      const obj = env.MEETING_ROOM.get(id);
      return obj.fetch(forward);
    }

    return new Response("Not Found", { status: 404 });
  }
};

// Re-export MeetingWorkflow so Wrangler recognizes it as an exported Workflow.
export { MeetingWorkflow } from "./workflow";